/* src/styles.css */

/* Import fonts */
@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap');

:root {
    --primary-color: #87CEFA; /* Light Sky Blue */
    --secondary-color: #ffffff;
    --accent-color: #008000; /* Green */
    --background-color: #E6F3FF; /* Light Baby Blue */
    --text-color: #333; /* Dark gray for text */
    --box-shadow: 0 10px 30px rgba(135, 206, 250, 0.1); /* Soft, modern shadow */
    --purple-glow: 0 0 20px rgba(0, 128, 0, 0.3); /* Green glow */
}

/* Dark mode variables */
[data-theme='dark'] {
    --secondary-color: #2c3e50;
    --background-color: #1a202c;
    --text-color: #f7fafc;
    --box-shadow: 0 8px 30px rgba(255, 255, 255, 0.05);
}

body {
    font-family: 'Roboto', sans-serif;
    margin: 0;
    padding: 0;
    background-color: var(--background-color);
    color: var(--text-color);
    line-height: 1.8;
}

main {
    margin-top: 80px;
    padding: 40px;
    max-width: 92%;
    margin-left: auto;
    margin-right: auto;
    box-sizing: border-box;
}

/* Header specific styles */
header {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px 40px;
    z-index: 1000;
    background-color: rgba(255, 255, 255, 0.8);
    backdrop-filter: blur(10px);
    box-shadow: var(--box-shadow);
    transition: background-color 0.3s ease, box-shadow 0.3s ease;
}

/* Navigation and other styles */
h1, h2, h3, h4, h5, h6 {
    color: var(--accent-color);
    margin-bottom: 20px;
    font-weight: 700;
}

a {
    color: var(--accent-color);
    text-decoration: none;
    transition: color 0.3s ease, transform 0.3s ease;
}

a:hover {
    color: var(--primary-color);
    transform: translateY(-2px);
}

.container {
    background-color: var(--secondary-color);
    border-radius: 20px;
    padding: 30px;
    margin-bottom: 30px;
    box-shadow: var(--box-shadow);
    transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.container:hover {
    transform: translateY(-5px);
    box-shadow: var(--purple-glow);
}

button {
    background-color: var(--accent-color);
    color: var(--secondary-color);
    border: none;
    padding: 14px 28px;
    border-radius: 8px;
    cursor: pointer;
    transition: all 0.3s cubic-bezier(0.25, 0.8, 0.25, 1);
    font-weight: 600;
    font-size: 16px;
    text-transform: uppercase;
    letter-spacing: 1px;
    position: relative;
    overflow: hidden;
}

button::before {
    content: '';
    position: absolute;
    top: 0;
    left: -100%;
    width: 100%;
    height: 100%;
    background: linear-gradient(
        120deg,
        transparent,
        rgba(255, 255, 255, 0.3),
        transparent
    );
    transition: all 0.6s cubic-bezier(0.22, 1, 0.36, 1);
}

button:hover::before {
    left: 100%;
}

button:hover {
    background-color: var(--primary-color);
    transform: translateY(-3px) scale(1.05);
    box-shadow: 0 7px 14px rgba(0, 0, 0, 0.1), 0 5px 5px rgba(0, 0, 0, 0.1);
}

button:active {
    transform: translateY(-1px) scale(1.02);
    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.1);
}

input, textarea {
    width: 100%;
    padding: 12px;
    margin-bottom: 15px;
    border: 2px solid var(--accent-color);
    border-radius: 8px;
    background-color: var(--secondary-color);
    color: var(--text-color);
    transition: border-color 0.3s ease, box-shadow 0.3s ease, transform 0.3s ease;
}

input:focus, textarea:focus {
    outline: none;
    border-color: var(--primary-color);
    box-shadow: var(--purple-glow);
    transform: translateY(-2px);
}

.card {
    background-color: var(--secondary-color);
    border-radius: 20px;
    padding: 30px;
    margin-bottom: 30px;
    box-shadow: var(--box-shadow);
    transition: transform 0.5s cubic-bezier(0.22, 1, 0.36, 1), box-shadow 0.5s cubic-bezier(0.22, 1, 0.36, 1);
}

.card:hover {
    transform: translateY(-10px) scale(1.03);
    box-shadow: var(--purple-glow), 0 20px 40px rgba(0, 0, 0, 0.1);
}

.flex-container {
    display: flex;
    flex-wrap: wrap;
    gap: 30px;
}

@media (max-width: 1200px) {
    .flex-container {
        flex-direction: column;
    }
}

@media (max-width: 768px) {
    main {
        padding: 20px;
        margin-top: 60px;
    }

    header {
        padding: 15px 20px;
    }

    button {
        padding: 12px 24px;
        font-size: 14px;
    }

    .container, .card {
        padding: 20px;
    }

    h1 {
        font-size: 24px;
    }

    h2 {
        font-size: 20px;
    }

    body {
        font-size: 14px;
    }
}

@media (max-width: 480px) {
    main {
        padding: 15px;
        margin-top: 50px;
    }

    header {
        padding: 10px 15px;
    }

    button {
        padding: 10px 20px;
        font-size: 12px;
    }

    .container, .card {
        padding: 15px;
    }

    h1 {
        font-size: 22px;
    }

    h2 {
        font-size: 18px;
    }

    body {
        font-size: 13px;
    }
}

/* Ensure text inputs and buttons are easily tappable on mobile */
@media (max-width: 768px) {
    input, textarea, button {
        font-size: 16px;
    }

    input[type="checkbox"], input[type="radio"] {
        transform: scale(1.5);
        margin-right: 10px;
    }
}

/* Improve readability on mobile */
@media (max-width: 768px) {
    body {
        line-height: 1.6;
    }

    p {
        margin-bottom: 15px;
    }
}

/* Adjust layout for smaller screens */
@media (max-width: 480px) {
    .flex-container {
        gap: 15px;
    }
}

/* Ensure proper viewport settings */
@-ms-viewport {
    width: device-width;
}

@viewport {
    width: device-width;
}
